@import '../../styles/variables.sass';

.myItemsWrapper {
  padding: 1rem 0 2rem 0;
  min-height: 40vh;
  // added so that scrollbar doesnt show up
  // on removing the last item
  overflow-y: hidden;
  .addTradeWrapper {
    display: flex;
    justify-content: flex-end;
    margin: 0 0 3rem 0; }
  .allItemsBtn {
    border-bottom: solid 2px; }
  .tradeBtn {
    color: $light-grey;
    font-size: .9rem;
    font-weight: 700;
    padding: 0;
    background: transparent;
    text-transform: uppercase;
    transition: all, .3s;
    cursor: pointer;
    &:hover {
      color: $black; } }
  .noItemHeading {
    max-width: 570px;
    line-height: 1.7;
    text-align: center;
    margin: 9rem auto 0 auto; } }




